.footer {
	position: relative;
	margin-top: rem(120);
	padding: rem(60) 0;
	// padding-bottom: ;
	background: linear-gradient(180deg, rgba(242, 231, 220, .4) 0%, rgba(242, 231, 220, 0) 100%);
	border-top: rem(1) solid #F2E7DC;

	.button-icon {
		position: absolute;
		top: rem(-20);
		left: 50%;
		margin-left: rem(-20);
		z-index: 1;

		background-color: white;

		border: rem(1) solid #F2E7DC;

		i {
			font-size: rem(10);
		}

		&:hover {
			border-color: $pink;
			background-color: $pink;

			i {
				color: white;

				&::before {
					color: white;
				}
			}
		}
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include min {
			position: relative;
		}
		
	}

	&_logo {
		max-width: rem(220);
		margin-right: rem(105);

		@include min {
			display: none;
		}
	}

	&_nav {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		width: rem(358);

		@include min {
			order: 99;
			width: auto;
			flex: 1 0 0;
		}

		@include min768 {
			flex: 0 0 100%;
			width: 100%;
			margin-bottom: rem(32);
			order: 1;

			// display: flex;
			// flex-direction: column;
		}

		ul {
			flex: 0 0 50%;

			@include min768 {
				flex: 0 0 100%;
				text-align: center;

				&:first-child {
					order: 2;
				}

				&:last-child {
					order: 1;
					margin-bottom: rem(32);
				}
			}

			li {
				margin-bottom: rem(16);
				font-weight: 600;

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-weight: 600;
				}
			}
		}
	}

	&_social {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		flex: 1 0 0;

		@include min {
			order: -1;
			justify-content: flex-start;
			margin-right: rem(-80);
		}

		@include min768 {
			flex: 0 0 100%;
			width: 100%;
			margin-bottom: rem(32);
			justify-content: center;
		}

		a {
			margin-right: rem(16);
			display: flex;
			align-items: center;
			justify-content: center;

			background: rgba(222, 214, 207, 0.3);
			border-radius: 50%;

			width: rem(40);
			height: rem(40);

			i {
				color: $pink;
				font-size: rem(16);
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&_copy {
		margin-left: auto;

		@include min {
			position: absolute;
			bottom: rem(-20);
			left: 0;
		}

		@include min768 {
			position: relative;
			bottom: auto;
			left: auto;
			width: 100%;
			text-align: center;
			order: 99;
		}

		ul {
			text-align: right;

			@include min {
				text-align: left;
			}

			@include min768 {
				text-align: center;
			}

			li {
				margin-bottom: rem(22);

				@include min {
					margin-bottom: rem(14);
				}
				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-size: rem(12);
					color: #797979;
					line-height: 115%;
					text-decoration: underline;

					&:hover {
						text-decoration: none;
					}
				}
			}
		}
	}
}