.section_gifts {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	margin-bottom: rem(120) !important;

	@include mw900 {
		&::before {
			content: "";
			position: absolute;
			top: rem(-50);
			right: rem(568);
			width: rem(143);
			height: rem(165);
			// background-size: rem(165);
			display: block;
			background: url(../img/cup.png) no-repeat;

		}
	}

	@include min {
		margin-bottom: rem(80);
		padding-top: rem(130);

		.w-6\/12 {
			width: 100%;

			&:first-child {
				order: 2;
				
			}
			&:last-child {
				order: 1;
				margin-bottom: rem(32);
			}
		}
	}

	.vector4,
	.vector6 {
		position: absolute;
		z-index: -1;

		@include min {
			display: none;
		}
	}

	.vector6 {
		left: rem(-50);
		top: 50%;
	}

	.vector4 {
		right: 0;
		top: 25%;
	}

	.text-title {
		margin-bottom: rem(48);

		@include min {
			margin-bottom: rem(40);
			text-align: center;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
		}

	}

	.subtitle {
		font-size: rem(24);
		line-height: rem(32);
		font-weight: 600;
		margin-bottom: rem(24);
		display: block;
	}

	.list-number {
		max-width: rem(510);

		@include min {
			max-width: 100%;
		}
	}

	&-panel {
		position: relative;
		background: rgba(255, 255, 255, 0.8);
		/* Shadow+Blur */

		box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
		0px 16px 32px rgba(209, 194, 194, 0.08);
		backdrop-filter: blur(16px);

		border-radius: rem(14);
		padding: rem(40);
		width: rem(522);

		@include min {
			width: 100%;
		}
		
		@include min768 {
			padding: rem(32) rem(16);
		}

		&-item {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			margin-bottom: rem(32);

			&:last-child {
				margin-bottom: 0;
			}

			.left {
				width: rem(268);

				@include min {
					width: rem(320);
				}

				@include min768 {
					width: rem(168);
				}

				.top,.bottom {
					display: block;
				}
				.top {
					margin-bottom: rem(8);
					font-size: rem(32);
					line-height: rem(40);
					font-weight: 600;

					@include min768 {
						font-size: rem(24);
						line-height: rem(34);
					}
				}
				.bottom {
					font-size: rem(24);
					line-height: rem(29);
					color: $pink;

					@include min768 {
						font-size: rem(18);
						line-height: rem(24);
					}
				}
			}

			.right {
				width: rem(140);
				height: rem(140);

				@include min768 {
					width: rem(120);
					height: rem(120);
					flex: 1 0 0;
				}
			}
		}
	}
}

.list-number {
	counter-reset: item;

	li {
		position: relative;
		padding-left: rem(48);
		display: block;
		margin-bottom: rem(16);

		line-height: rem(24);
		font-size: rem(16);

		&:last-child {
			margin-bottom: 0;
		}

		&::before {
			content: counter(item);

			position: absolute;
			top: 0;
			left: 0;
			counter-increment: item;
			font-size: rem(14);
			font-weight: bold;
			color: $pink;

			width: rem(32);
			height: rem(32);
			border-radius: 50%;
			border: rem(1) solid $pink;
			background-color: white;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}
}