.section_about {
	position: relative;
	width: 100%;
	background: linear-gradient(180deg, rgba(242, 231, 220, .4) 0%, rgba(242, 231, 220, 0) 100%);
	padding: rem(120) 0;

	@include min {
		padding: rem(80) 0;
	}

	@include min768 {
		padding: rem(64) 0;
	}

	.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;

		@include min {
			.w-6\/12 {
				width: 100%;

				&:first-child {
					padding-top: rem(65);
				}
			}
		}

		@include min768 {
			.w-6\/12 {

				&:first-child {
					padding-top: rem(110);
				}
			}
		}

		// &::before {
		// 	content: "";
		// 	position: absolute;
		// 	top: rem(-182);
		// 	right: rem(-147);

		// 	width: rem(347);
		// 	height: rem(376);
		// 	background: url(../img/weight_plate.png) no-repeat;

		// 	@include min {
		// 		width: rem(90);
		// 		height: rem(110);
		// 		top: rem(-110);
		// 		right: 0;
		// 		background-size: cover;
		// 	}

		// 	@include min768 {
		// 		display: none;
		// 	}
		// }


	}

	&-right {
		margin-left: rem(100);

		@include min {
			margin-left: 0;
		}

		@include min768 {
			margin-top: rem(115);
		}

		.line {
			margin-bottom: rem(40);

			@include min {
				display: none;
			}
		}

		.text-title {
			// font-size: rem(48);
			// line-height: rem(56);
			// font-weight: 700;
			display: block;
			margin-bottom: rem(30);

			@include min {
				position: absolute;
				width: 100%;
				text-align: center;
				top: 0;
				left: 0;
			}

			// span {
			// 	color: $pink;
			// }
		}

		.text-first {
			font-size: rem(24);
			line-height: rem(34);
			color: #80827F;
			margin-bottom: rem(10);

			@include min {
				text-align: center;
				padding: 0 rem(30);
			}

			@include min768 {
				font-size: rem(18);
				line-height: rem(24);
				padding: 0;
			}
		}
		.text-last {
			color: #80827F;
			line-height: rem(22);
			margin-bottom: rem(40);

			@include min {
				text-align: center;
				margin-bottom: rem(24);
				padding: 0 rem(45);
			}

			@include min768 {
				font-size: rem(14);
				line-height: rem(20);
				padding: 0;
			}
		}
	}

	&-bottom {
		margin-top: rem(80);
		display: flex;
		flex-direction: column;
		align-items: center;

		p {
			font-size: rem(24);
			line-height: rem(34);
			margin-bottom: rem(31);
			width: 60%;

			@include min {
				width: 100%;
			}

			@include min768 {
				font-size: rem(18);
				line-height: rem(24);
			}
		}


	}
}

.image-text {
	display: flex;
	flex-wrap: wrap;

	@include min768 {
		align-items: center;
	}

	&_img {
		flex: 0 0 rem(80);
		height: rem(80);
		margin-right: rem(24);

		@include min768 {
			flex: 0 0 rem(60);
			height: rem(60);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	p {
		flex: 1 0 0;
		font-size: rem(20);
		line-height: rem(30);
		color: #80827F;

		@include min768 {
			font-size: rem(18);
			line-height: rem(24);
		}
	}
}

.block-sertificate {
	position: absolute;
	right: 0;
	bottom: 0;
	width: rem(237);
	padding: rem(32) rem(20);
	overflow: hidden;
	border-radius: rem(12);
	text-align: center;

	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	backdrop-filter: blur(16px);
	background: rgba(255, 255, 255, 0.8);

	@include min768 {
		width: 100%;
		height: rem(195);
		padding: rem(16) rem(24);
		display: flex;
		bottom: rem(-84);
		// align-items: center;
		flex-direction: column;
		justify-content: center;

		@include min375 {
			height: rem(140);
		}

		img {
			width: rem(120);
			position: absolute;
			top: rem(16);
			right: rem(24);

			@include min375 {
				width: rem(75);
			}
		}
	}

	.title {
		font-size: rem(20);
		font-weight: 600;
		line-height: 120%;
		margin-bottom: rem(10);
		display: block;

		@include min768 {
			width: rem(170);
		}
	}
	.text {
		line-height: 120%;
		margin-bottom: rem(24);

		@include min768 {
			width: rem(170);
			font-size: rem(14);
			margin-bottom: 0;
			text-align: left;
		}
	}

}