.section_start {
	position: relative;

	.container {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			top: rem(-60);
			right: rem(-227);
			width: rem(1018);
			height: rem(958);
			// height: 100%;
			object-fit: cover;
			// opacity: .05;
			background: url('../img/header_bg.png') no-repeat;
			z-index: -1;

			@include min {
				width: rem(784);
				height: rem(604);
				right: rem(-40);
				background: url('../img/header_bg_768.png') no-repeat;
			}

			@include min660 {
				right: 0;
				width: 100%;
				height: 100%;
				background-size: 100%;
			}

			@include min540 {
				// position: relative;
				// right: auto;
				// top: auto;
				width: rem(373);
				height: rem(386);
				right: rem(-30);
				top: rem(-90);
				background: url('../img/header_bg_480.png') no-repeat;
			}
		}

		@include min {
			.w-6\/12 {
				width: 100%;

				&:first-child {
					order: 2;
				}

				&:last-child {
					order: 1;
				}
			}
		}

		@include min540 {
			.w-6\/12 {
				&:last-child {
					height: rem(186);
				}
			}
		}

		.photo_card {
			position: absolute;
			bottom: 0;
			right: rem(-72);

			@include min {
				right: 0;
			}

			@include min540 {
				display: none;
			}
		}
	}

	
	.title {
		font-size: rem(64);
		line-height: rem(64);
		font-weight: 600;
		margin-bottom: rem(32);

		padding-top: rem(158);

		@include min {
			padding-top: rem(90);
			width: 100%;
			text-align: center;

			br {
				display: none;
			}
		}

		@include min540 {
			font-size: rem(40);
			line-height: rem(40);
		}

		span {
			color: $pink;
		}
	}

	.text {
		font-size: rem(20);
		font-weight: 600;
		margin-bottom: rem(48);

		@include min {
			text-align: center;
			margin-bottom: rem(32);
		}

		@include min540 {
			font-size: rem(16);
		}
	}

	&-buttons {
		display: flex;
		flex-wrap: wrap;

		@include min {
			justify-content: center;
		}

		@include min540 {
			.button-default,
			.button-white {
				width: 100%;
				text-align: center;
			}

			.button-default {
				margin-bottom: rem(12);
			}
		}

		.button-default {
			margin-right: rem(16);
		}
	}

	.line {
		margin-top: rem(100);

		@include min {
			display: none;
		}
	}

	.block_advach {
		margin-top: rem(128);
	}

	// &-right {
	// 	position: absolute;
	// 	top: 0;
	// 	right: 0;
	// 	z-index: -1;
	// 	opacity: .2;
	// 	width: rem(554);
	// }
}

.block_advach {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	width: rem(244);
	padding: rem(32);

	border-radius: rem(14);
	box-shadow: 0px 24px 32px rgba(209, 194, 194, 0.14),
	0px 24px 56px rgba(209, 194, 194, 0.12);

	@include min {
		width: rem(200);
		background-color: white;
		margin-left: rem(20);
		padding: rem(80) rem(24) rem(24) rem(24);
	}

	@include min540 {
		display: none;
	}

	&-image {
		position: absolute;
		top: rem(-20);
		left: rem(-10);
		width: rem(266);

		@include min {
			width: rem(162);
			left: auto;
			right: rem(10);
		}
	}

	&-item {
		margin-bottom: rem(30);

		@include min {
			margin-bottom: rem(24);
		}

		&:last-child {
			margin-bottom: 0;
		}
		.title {
			color: $pink;
			font-size: rem(56);
			line-height: rem(64);
			margin-bottom: rem(20);
			// display: block;

			@include min {
				font-size: rem(44);
				line-height: rem(54);
				margin-bottom: rem(14);
				text-align: left !important;
			}
		}
		.text {
			font-size: rem(18);
			line-height: 120%;
			color: #80827F;
			margin-bottom: 0;

			@include min {
				font-size: rem(16);
				text-align: left !important;
			}
		}
	}
}

.photo_card {
	width: rem(112);
	height: rem(112);
	border-radius: 50%;
	border: rem(6) solid white;
	// transform: matrix(1, 0, 0, -1, 0, 0);
	box-shadow: 0px 11px 60px rgba(0, 0, 0, 0.1);
	overflow: hidden;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

