.section_social {
	background: linear-gradient(270deg, #FFFFFF 0%, #FFEDEF 100%);
	/* Main_Shadow */

	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	border-radius: rem(14);
	height: rem(528);

	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	margin-bottom: rem(120);

	@include min {
		margin-bottom: rem(80);
		height: auto;
	}

	@include min540 {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			top: rem(-35);
			left: 50%;
			margin-left: rem(-42);
			width: rem(83);
			height: rem(83);
			background: url(../img/icon-fire_pink.png) no-repeat;
		}
	}
	

	&-left {
		position: relative;
		width: rem(516);
		margin-right: rem(26);

		@include min {
			display: none;
		}

		img {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: rem(583);
		}
	}

	&-right {
		flex: 1 0 0;
		padding: rem(52) 0;

		@include min {
			width: 100%;
			padding: rem(40);
		}

		@include min540 {
			padding: rem(32) rem(8);
		}

		.text-title {
			margin-bottom: rem(24);

			@include min {
				max-width: rem(320);
			}

			@include min540 {
				max-width: 100%;
				margin-bottom: rem(14);
			}
		}

		.text {
			margin-bottom: rem(32);
			font-weight: 600;
			max-width: rem(485);

			@include min {
				font-size: rem(24);
				line-height: rem(29);
			}

			@include min540 {
				font-size: rem(18);
				line-height: rem(24);
				text-align: center;
				margin-bottom: rem(24);
			}

			span {
				color: $pink;
			}
		}

		.button-default {
			margin-bottom: rem(56);

			@include min540 {
				margin-bottom: rem(30);
				text-align: center;
			}
		}

		@include min540 {
			.smart-center {
				display: flex;
				justify-content: center;
			}
		}
	}
}