.section_count_3 {
	// margin-bottom: rem(136);
	.w-6\/12 {
		width: calc(50% - 0.625rem);

		@include min {
			width: 100%;
		}
	}

	.text-title {
		max-width: 50%;

		@include min {
			max-width: 100%;
		}
		
	}
}

.list_group {
	display: flex;
	flex-wrap: wrap;

	background: linear-gradient(270deg, #FFFFFF 0%, #FFEDEF 100%);
	/* Main_Shadow */

	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);

	border-radius: rem(14);

	height: rem(340);

	@include min {
		margin-bottom: rem(24);
	}

	@include min768 {
		position: relative;
		background: transparent;
		box-shadow: none;
		border-radius: 0;
		flex-direction: column;
		flex-wrap: nowrap;
		margin-bottom: rem(48);
	}

	&-image {
		// flex: 0 0 auto;
		width: rem(242);
		margin-right: rem(14);

		@include min768 {
			width: 100%;
			height: rem(110);
			border-radius: rem(14);
			margin-right: 0;
			display: flex;
			background: linear-gradient(270deg, #FFFFFF 0%, #FFEDEF 100%);
			/* Main_Shadow */

			box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
			0px 16px 32px rgba(209, 194, 194, 0.08);

			img {
				max-width: rem(85);
				height: auto !important;
			}
		}

		img {
			// width: 100%;
			height: rem(340);
			// object-fit: cover;
		}
	}

	&-text {
		flex: 1 0 0;
		// margin-left: rem(48);
		padding: rem(40) rem(30) rem(40) 0;

		.title-24 {
			margin-bottom: rem(16);
			display: block;

			@include min768 {
				font-size: rem(18);
				position: absolute;
				top: rem(40);
				left: rem(85);
			}
		}

		.list {
			li {
				margin-bottom: rem(8);
			}
		}

	}
}

.title-24 {
	font-size: rem(24);
	font-weight: 600;
	line-height: rem(30);
	color: #3D4039;
}