.section_reviews {
	background: linear-gradient(180deg, rgba(242, 231, 220, .4) 0%, rgba(242, 231, 220, 0) 100%);
	padding: rem(120) 0 rem(80) 0;

	@include min {
		padding: rem(40) 0 rem(40) 0;
		background: transparent;
	}

	.swiper-slide .container {
		width: 100%;

		@include min {
			width: calc(100% - 30px);
		}
	}

	&-arrow {
		.arrow_control {
			display: flex;
			flex-wrap: wrap;

			.arrow-button {
				&:first-child {
					margin-right: rem(8);
				}
			}
		}

		@include min768 {
			display: none;
		}
	}

	.top {
		margin-bottom: rem(48);

		@include min {
			margin-bottom: rem(28);
		}

		@include min768 {
			>.flex {
				justify-content: center;
			}
		}
	}

	.slider-wr {
		margin-bottom: rem(70);

		@include min {
			margin-bottom: rem(40);
		}
	}

	.section_tabs-item {
		border-radius: rem(28);

		box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
		0px 16px 32px rgba(209, 194, 194, 0.08);
		background-color: white;
		padding: rem(40);

		@include min768 {
			background-color: transparent;
			padding: 0;
			border-radius: 0;
		}

		.tabs_item-left {
			width: rem(580);

			@include min {
				width: 100%;
			}
		}
		
		.box {
			max-width: rem(415);

			@include min {
				max-width: 100%;
			}
		}

		.tabs_item-right {
			flex: 1 0 0;
		}
	}

	.swiper-container {

		.arrow_control {
			display: flex;
			justify-content: center;

			&-nav {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				.swiper-pagination-bullet {
					margin-right: rem(32);

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}
	}
}