.section_tabs {

	padding-top: rem(178);
	margin-bottom: rem(120);

	@include min {
		padding-top: rem(80);
		margin-bottom: rem(80);
	}

	.text-title {
		margin-bottom: rem(52);
		text-align: center;

		@include min540 {
			> span {
				display: block;
			}
			margin-bottom: rem(32);
		}
	}

	&-nav {
		display: flex;
		justify-content: space-between;
		// overflow-x: scroll;
		border-bottom: rem(1) solid #DED6CF;
		margin-bottom: rem(40);

		@include min {
			position: relative;
			margin-bottom: rem(24);
			border-bottom: 0;
			flex-wrap: wrap;
			flex-direction: column;

			.selected {
				position: relative;
				height: rem(48);
				border-radius: rem(14);
				background-color: white;
				border: rem(1) solid $pink;
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 0 rem(24);
				color: $pink;
				font-size: rem(16);
				line-height: rem(22);
				font-weight: 600;

				&::after {
					content: "\e900";
					font-family: 'icons';
					font-size: rem(8);
					transition: all 300ms ease;
				}
			}
			

			
		}

		button {
			font-weight: 500;
			color: #A4A4A4;
			padding-top: rem(10);
			padding-bottom: rem(24);
			font-size: rem(16);
			position: relative;
			// margin-right: rem(40);
			transition: all 400ms ease;
			white-space: nowrap;

			@include min {
				width: 100%;
				
			}

			// &:last-child {
			// 	margin-right: 0;
			// }

			&::after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: rem(1);
				background-color: $pink;
				opacity: 0;
				transition: all 400ms ease;

				@include min {
					display: none;
				}
			}

			&:hover {
				color: $pink;

				&::after {
					opacity: 1;
				}
			}

			
		}
		button.active {
			color: $pink;

			&::after {
				opacity: 1;
			}
		}
	}

	&-item {
		display: flex;
		flex-wrap: wrap;

		.swiper-container {
			width: 100%;
			height: rem(510);

			@include min {
				// height: rem(810);
				height: 100%;
			}
		}

		.swiper-slide {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			align-items: center;
		}

		.tabs_item-left {
			position: relative;
			width: rem(660);
			height: rem(510);

			@include min {
				width: 100%;
				height: rem(422);
			}

			@include min768 {
				height: rem(220);
				margin-bottom: rem(20);
			}

			.box-image {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				@include min {
					height: 100%;
				}

				
				
				img {
					width: calc(50% - 0.5rem);
					height: 100%;
					object-fit: cover;
					object-position: top;
					border-radius: rem(14);
				}
			}

			.box-text {
				position: absolute;
				bottom: rem(-10);
				right: rem(-10);
				padding: rem(16);
				border-radius: rem(12);
				background-color: white;
				z-index: 10;
				max-width: rem(180);

				box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
				0px 16px 32px rgba(209, 194, 194, 0.08);

				@include min {
					right: rem(10);
					// bottom: rem(10);
				}

				@include min768 {
					max-width: 100%;
					width: calc(100% - 1.2rem);
					font-size: rem(14);
					text-align: center;
					bottom: rem(-24);

				}

				> span {
					color: #80827F;
					line-height: rem(22);
					font-weight: 600;

					> span {
						color: $pink;
						text-transform: uppercase;
						display: block;

						@include min768 {
							display: inline-block;
							margin-left: rem(4);
						}
					}
				}
			}
		}

		.tabs_item-right {
			position: relative;
			display: flex;
			// align-items: center;
			// flex-direction: column;
			align-items: center;
			justify-content: space-between;
			flex-grow: 1;

			.box {
				
				max-width: rem(317);
				margin-left: rem(100);

				@include min {
					max-width: 100%;
					margin-left: 0;
					margin-top: rem(24);
				}

				.border-text-b {
					margin-bottom: rem(24);

				}

				.icon-quote {
					position: absolute;
					top: 0;
					left: rem(100);
					font-size: rem(26);

					@include min {
						position: relative;
						left: auto;
						top: auto;
						margin-bottom: rem(24);
					}

					@include min768 {
						font-size: rem(17);
						margin-bottom: rem(18);
					}
				}

				.title {
					font-size: rem(32);
					font-weight: 600;
					margin-bottom: rem(24);

					@include min768 {
						font-size: rem(24);
						line-height: rem(30);
						margin-bottom: rem(8);
					}
				}

				.list {
					margin-bottom: rem(48);
				}

				.text {
					font-size: rem(16);
					line-height: rem(24);
					color: #80827F;
					margin-bottom: rem(32);

					@include min {
						margin-bottom: rem(24);
					}

					@include min768 {
						font-size: rem(14);
						line-height: rem(20);
					}
				}

				@include min768 {
					.button-default-b {
						width: 100%;
						text-align: center;
					}
				}
				
			}

			

			
		}

		.arrow_control.desktop {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			max-width: rem(40);
			position: absolute;
			top: 50%;
			margin-top: rem(-86);
			right: 0;
			z-index: 10;

			@include min {
				display: none;
			}

			.arrow-top {
				margin-bottom: rem(46);

				
			}

			.arrow-bottom {
				margin-top: rem(46);
			}

			.arrow_control-nav {
				display: flex;
				flex-direction: column;

				

				
			}
		}

		.control_smart {
			display: none;

			@include min {
				display: block;
				margin-top: rem(40);
				.arrow_control {
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
					align-items: center;

					&-nav {
						display: flex;
						align-items: center;
						flex-wrap: wrap;

						.swiper-pagination-bullet {
							margin-right: rem(12);
							margin-bottom: 0 !important;

							&:last-child {
								margin-right: 0;
							}
						}
					}
				}
			}
		}

		
	}
}

.list {
	li {
		position: relative;
		padding-left: rem(20);
		font-size: rem(16);
		color: #80827F;
		line-height: 120%;
		margin-bottom: rem(16);

		&:last-child {
			margin-bottom: 0;
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(6);
			left: 0;
			width: rem(8);
			height: rem(8);
			border-radius: 50%;
			background-color: $pink;
		}
	}
}

.border-text-b {
	display: inline-block;
	font-size: rem(11);
	font-weight: 700;
	color: $pink;
	line-height: rem(16);
	padding: rem(7) rem(12);
	border: rem(1) solid $pink;
	border-radius: rem(8);
}

.arrow-button {
	width: rem(40);
	height: rem(40);
	border-radius: 50%;
	border: rem(1) solid #DED6CF;

	display: flex;
	align-items: center;
	justify-content: center;

	i {
		font-size: rem(10);
		color: $pink;

		&::before {
			color: $pink;
		}
	}
	i.icon-arrow-left,
	i.icon-arrow-right {
		font-size: rem(16);
	}
}

.swiper-pagination-bullet-active {
	width: rem(8) !important;
	height: rem(8) !important;
	border-radius: 50%;
	background-color: white !important;
	border: rem(2) solid $pink;
}

.swiper-pagination-bullet {
	width: rem(6);
	height: rem(6);
	border-radius: 50%;
	background-color: #DED6CF;
	margin-bottom: rem(12);
	opacity: 1 !important;

	&:last-child {
		margin-bottom: 0;
	}
}

@include min {
	.tabs_button_wrap {
		position: absolute;
		width: 100%;
		top: rem(50);
		left: 0;
		box-shadow: 0px 24px 32px rgba(209, 194, 194, 0.14),
		0px 24px 56px rgba(209, 194, 194, 0.12);
		background-color: white;
		border-radius: rem(14);
		padding: rem(14);
		margin-top: rem(8);
		z-index: 10;
		display: none;

		button {
			height: rem(48);
			border-radius: rem(14);
			background-color: white;
			border: rem(1) solid $pink;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0 rem(24);
			color: $pink;
			font-size: rem(16);
			line-height: rem(22);
			font-weight: 600;
			margin-bottom: rem(14);
			width: 100%;

			&:last-child {
				margin-bottom: 0;
			}
		}

		button.active {
			background-color: $pink;
			color: white;
		}
	}


	.section_tabs-nav.active {
		.tabs_button_wrap {
			display: block;
		}

		.selected {
			&::after {
				content: "\e903";
			}
		}
	}
}