.section_plan {
	// display: flex;
	// flex-wrap: wrap;
	// justify-content: space-between;

	margin-top: rem(150);
	margin-bottom: rem(120);
	position: relative;

	// &::after {
	// 	content: "";
	// 	position: absolute;
	// 	right: rem(-93);
	// 	bottom: rem(27);
	// 	width: rem(187);
	// 	height: rem(102);
	// 	background: url(../img/tr/vector1.png) no-repeat;
	// }

	@include min {
		margin-top: rem(40);
	}

	@include min540 {
		margin-top: rem(20);
		margin-bottom: rem(64);

		.swiper-wrapper {
			padding-top: rem(70);
		}
	}

	// @include mw540 {
	// 	.swiper-wrapper {
	// 		height: rem(538) !important;
	// 	}
	// }

	.swiper-slide {
		padding-top: rem(70);
	}

	.plan_item {
		position: relative;
		width: calc(33.333% - 00.625rem);
		border-radius: rem(14);
		border: rem(1) solid #F2E7DC;
		padding: rem(128) rem(28) rem(40) rem(28);

		height: rem(538);

		@include min {
			width: 100%;
			height: auto;
			margin-bottom: rem(24);

			padding: rem(32) rem(40) rem(32) rem(18);
			display: flex;
			flex-wrap: wrap;
		}

		@include min540 {
			padding: rem(83) rem(40) rem(32) rem(18);
		}

		&-image {
			position: absolute;
			top: rem(-100);
			left: 50%;
			margin-left: rem(-100);

			@include min {
				position: relative;
				top: auto;
				left: auto;
				margin-left: 0;
				margin-right: rem(85);
			}

			@include min540 {
				position: absolute;
				top: rem(-60);
				left: 50%;
				margin-left: rem(-60);

				img {
					width: rem(118);
					height: rem(118);
					object-fit: cover;
				}
			}
		}

		&-text {
			max-width: rem(360);
			text-align: center;

			@include min {
				text-align: left;
				flex: 1 0 0;
			}

			@include min540 {
				text-align: center;
				max-width: 100%;
			}
			
			.title-24 {
				margin-bottom: rem(24);
			}

			// .title {
			// 	font-size: rem(24);
			// 	line-height: rem(30);
			// }
		}
	}
}

.list-lite {

	@include min540 {
		
	}

	li {
		position: relative;
		font-size: rem(16);
		color: #80827F;
		display: flex;
		align-items: center;
		flex-direction: column;
		margin-bottom: rem(8);

		@include min {
			text-align: left;
			align-items: flex-start;
		}

		@include min540 {
			font-size: rem(14);
			padding: 0 rem(25);
			text-align: center;
			align-items: center;
		}

		&::after {
			content: "";
			background: url(../img/gray_line.png) no-repeat;
			width: rem(80);
			height: rem(14);
			background-size: 100%;
			display: block;
			margin-top: rem(14);
		}

		&:last-child {
			margin-bottom: 0;

			&::after {
				display: none;
			}
		}
	}
}