.block_faq {
	margin-top: rem(80);

	@include min {
		margin-top: rem(40);
	}

	&-item {
		display: flex;
		flex-direction: column;
		border-bottom: rem(1) solid #F2E7DC;

		.faq_item-header {
			cursor: pointer;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			padding: rem(32) 0;

			transition: all 300ms ease;

			@include min768 {
				padding: rem(12) 0;
			}

			.title {
				font-size: rem(20);
				line-height: 120%;
				font-weight: 600;

				@include min768 {
					font-size: rem(18);
					line-height: rem(24);
					max-width: rem(200);
				}
			}
		}

		.faq_item-text {
			line-height: 140%;
			color: #80827F;
			width: 54%;
			display: none;
			padding-bottom: rem(32);

			@include min {
				width: 100%;
				font-size: rem(16);
				line-height: rem(22);
			}

			@include min768 {
				font-size: rem(14);
				line-height: rem(20);
				padding-bottom: rem(12);
			}
		}

		&:hover {
			.button-icon {
				background-color: $pink;

				i {
					&::before {
						color: white;
					}
				}

				// i {
				// 	&::before {
				// 		display: none;
				// 	}

				// 	&::after {
				// 		content: "";
				// 		height: rem(2);
				// 		width: rem(16);
				// 		background-color: white;
				// 		display: block;
				// 		border-radius: rem(3);
				// 	}
				// }
			}
		}
	}

	&-item.active {
		.faq_item-header {
			padding: rem(32) 0 rem(22) rem(0);
		}

		.faq_item-text {
			display: block;
		}

		.button-icon {
			background-color: $pink;

			i {
				&::before {
					display: none;
				}

				&::after {
					content: "";
					height: rem(2);
					width: rem(16);
					background-color: white;
					display: block;
					border-radius: rem(3);
				}
			}
		}
	}
}