.section_selected {
	position: relative;

	width: 100%;
	background: linear-gradient(180deg, rgba(242, 231, 220, .4) 0%, rgba(242, 231, 220, 0) 100%);

	padding: rem(120) 0;

	@include min {
		padding: rem(80) 0;
	}

	
}
.item_select-buffer {
	display: none;

	@include min540 {
		display: none;
		padding: 0 rem(16);
		margin-bottom: rem(16);

		.text {
			font-size: rem(14);
			line-height: rem(20);
			color: #80827F;
			margin-bottom: rem(16);
		}
	}
}

.item_select-buffer.active {
	display: none;

	@include min540 {
		display: block;
	}
}

