.text-title {
	text-align: center;
	margin-bottom: rem(80);

	@include min {
		margin-bottom: rem(40);
		padding: 0 rem(30);
	}
}

.text-title-50 {
	margin-bottom: rem(24);

	@include min {
		width: 100%;
	}
}

.item_select {
	display: flex;
	flex-wrap: wrap;
	border-radius: rem(14);
	padding-top: rem(32);
	padding-right: rem(24);
	background: linear-gradient(270deg, #FFFFFF 0%, #FFEDEF 100%);
	/* Main_Shadow */

	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
		0px 16px 32px rgba(209, 194, 194, 0.08);

	width: calc(50% - 0.625rem);
	height: rem(312);
	margin-bottom: rem(62);

	@include min {
		width: 100%;
		// margin-bottom: rem(48);
		height: rem(343);
	}

	@include min540 {
		height: rem(110);
		padding-right: 0;
		padding-top: 0;
		padding-left: rem(16);
		margin-bottom: rem(16);
		justify-content: space-between;
	}

	&-left {
		position: relative;
		flex: 0 0 40%;

		@include min540 {
			order: 2;
			height: 100%;
			flex: 0 0 rem(120);
		}

		img {
			position: absolute;
			top: rem(-30);
			left: 0;
			width: 100%;

			@include min540 {
				top: -0.8rem;
				display: block;
				height: rem(122);
				object-fit: cover;
				max-width: rem(120);
			}
		}
	}

	&-right {
		flex: 0 0 60%;

		.open {
			display: none;
		}

		@include min540 {
			order: 1;

			display: flex;
			flex-direction: column;
			justify-content: center;

			.open {
				display: flex;
				align-items: center;
				font-size: rem(12);
				line-height: rem(14);
				font-weight: 600;
				color: $pink;

				i {
					margin-left: rem(8);
					font-size: rem(6);
					color: $pink;
				}
			}



			.smart-block {
				display: none;
			}
		}

		.title {
			font-weight: 600;
			font-size: rem(20);
			line-height: rem(30);
			margin-bottom: rem(12);
			display: block;

			@include min540 {
				font-size: rem(18);
				line-height: rem(24);
			}
		}

		.text {
			color: #80827F;
			font-size: rem(14);
			line-height: rem(20);
			margin-bottom: rem(34);
			max-width: rem(330);
		}
	}

	&-right.active {
		position: relative;

		.open {
			i {
				&::before {
					content: "\e903";
				}
			}
		}

		.smart-block {
			// position: absolute;
			display: block;
			// bottom: 0;
			// left: 0;
		}
	}
}
