.block_choice {
	position: relative;
	
	border-radius: rem(14);
	padding: rem(67) rem(51) rem(67) rem(40);
	background: rgba(255, 255, 255, 0.8);
	/* Shadow+Blur */

	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	backdrop-filter: blur(16px);
	// filter: blur(16px);

	@include min {
		position: absolute;
		top: rem(-200);
		left: 0;
		width: 100%;
	}

	@include min540 {
		top: rem(-100);
	}

	@include min540 {
		padding: rem(32) rem(18);
	}

	// &::after {
	// 	content: "";
	// 	position: absolute;
	// 	// top: rem(-93);
	// 	bottom: rem(-46);
	// 	right: rem(-52);
	// 	width: rem(171);
	// 	height: rem(175);

	// 	background: url(../img/heart.png) no-repeat;
	// 	// z-index: -1;

	// 	@include min {
	// 		display: none;
	// 	}
	// }

	&-item {
		display: flex;
		// flex-wrap: wrap;
		margin-bottom: rem(40);

		&:last-child {
			margin-bottom: 0;
		}

		.choice_item-image {
			flex: 0 0 rem(80);
			width: rem(80);
			height: rem(80);
			border-radius: rem(20);
			overflow: hidden;
			margin-right: rem(24);
			@include min540 {
				flex: 0 0 rem(60);
				width: rem(60);
				height: rem(60);
				margin-right: rem(16);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.choice_descr {
			flex: 0 0 1;
			.title {
				font-size: rem(24);
				line-height: rem(30);
				font-weight: 600;
				display: block;
				margin-bottom: rem(16);

				@include min540 {
					font-size: rem(18);
					line-height: rem(24);
				}
			}

		}
	}
}

.button-plus {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	line-height: rem(22);
	font-weight: 600;
	color: $pink;

	.plus {
		margin-right: rem(8);
		border-radius: 50%;
		width: rem(15);
		height: rem(15);
		border: rem(1.5) solid $pink;
		display: flex;
		align-items: center;
		justify-content: center;

		i {
			font-size: rem(9);
			font-weight: 600;
			color: $pink;
		}
	}
}

.block_choice-right {
	flex: 1 0 0;

	@include min {
		width: 100%;
		flex: 0 0 100%;
		order: 1;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}