.section_advantages {
	width: 100%;
	background: linear-gradient(180deg, rgba(242, 231, 220, .4) 0%, rgba(242, 231, 220, 0) 100%);

	padding-top: rem(76);

	margin-bottom: rem(120);

	@include min {
		margin-bottom: rem(80);
		padding-top: rem(40);
	}

	.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		// &::before {
		// 	content: "";
		// 	position: absolute;
		// 	top: rem(-190);
		// 	left: rem(-187);

		// 	width: rem(347);
		// 	height: rem(376);
		// 	background: url(../img/weight_plate.png) no-repeat;

		// 	@include min {
		// 		left: auto;
		// 		right: rem(-65);
		// 		top: rem(-80);
		// 		width: rem(176);
		// 		height: rem(156);
		// 		background-size: rem(156);
		// 	}

		// 	@include min540 {
		// 		display: none;
		// 	}
		// }
	}

	&-left {
		position: relative;
		width: rem(325);
		padding-top: rem(235);

		@include min {
			width: 100%;
			margin-bottom: rem(65);
			padding-top: 0;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
		}

		@include min540 {
			margin-bottom: rem(32);
			flex-wrap: nowrap;
		}

		.image {
			position: absolute;
			top: 0;
			left: 0;

			@include min {
				position: relative;
			}

			@include min540 {
				max-width: rem(117);
			}
		}

		.text-title {
			position: relative;
			font-size: rem(48);
			line-height: rem(56);
			font-weight: 600;
			margin-bottom: rem(64);

			@include min540 {
				margin-bottom: 0;
				font-size: rem(28);
				line-height: rem(34);
			}

			// &::after {
			// 	content: "";
			// 	// width: rem(158);
			// 	// height: rem(18);
			// 	// display: block;
			// 	// background: url(../img/header_line.png) no-repeat;
			// 	// background-size: rem(18);
			// 	display: block;
			// 	margin-top: rem(65);

			// 	@extend .line;

			// 	@include min {
			// 		margin-top: rem(18);
			// 	}
			// }

			@include min {
				flex: 1 0 0;
				margin-left: rem(32);
			}
		}

		@include min {
			// .line {
			// 	display: none;
			// }
		}
	}

	&-center {
		width: rem(430);

		@include min {
			width: rem(375);
			margin-right: rem(40);
		}

		@include min540 {
			width: 100%;
			margin-right: 0;
		}
	}

	&-right {
		position: relative;
		width: rem(305);
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		align-items: center;

		@include min {

		}

		@include min540 {
			width: 100%;
		}

		// &::after {
		// 	content: "";
		// 	position: absolute;
		// 	top: rem(-40);
		// 	right: rem(-62);
		// 	width: rem(203);
		// 	height: rem(124);
		// 	background: url(../img/tr/vector2.png) no-repeat;
		// 	z-index: 0;

		// 	@include min540 {
		// 		display: none;
		// 	}
		// }

		.block-blue {
			position: relative;
			z-index: 1;
			background: rgba(255, 255, 255, 0.8);
			/* Shadow+Blur */

			box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
			0px 16px 32px rgba(209, 194, 194, 0.08);
			backdrop-filter: blur(16px);

			border-radius: rem(14);
			padding: rem(32) rem(24) rem(44) rem(24);
			width: 100%;
			
			@include min540 {
				padding: rem(24) rem(26);
			}

			.title {
				font-size: rem(24);
				line-height: rem(29);
				font-weight: 500;
				display: block;
				text-align: center;
				margin-bottom: rem(30);

				@include min540 {
					font-size: rem(18);
					line-height: rem(24);
				}
			}

			.swiper-slide {

				@include min540 {
					text-align: center;
				}

				> span {
					font-size: rem(16);
					font-weight: 600;
					display: block;
					text-align: center;
					margin-top: rem(24);
				}
			}

			
		}

		.arrow_control {
			margin-top: rem(32);
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			width: rem(220);

			@include min540 {
				width: 100%;
				margin-top: rem(24);
			}

			&-nav {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				.swiper-pagination-bullet {
					margin-right: rem(12);
					margin-bottom: 0;

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}
	}
}

.block-article {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;

	margin-bottom: rem(30);

	&:last-child {
		margin-bottom: 0;
	}

	&_image {
		max-width: rem(96);
		margin-right: rem(16);
	}

	&_text {
		flex: 1 0 0;

		.text {
			font-size: rem(16);
			line-height: rem(24);
			color: #80827F;
		}
	}
}