.header-position {
	position: relative;
	height: rem(100);
}



.header {
	// position: sticky;
	// top:0;
	// z-index: 50;
	// transition: all 400ms ease;

	background: linear-gradient(180deg, #F2E7DC 0%, rgba(242, 231, 220, 0) 100%);

	> .container {
		padding: rem(24) 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		transition: all 400ms ease;
	}
		

	&_logo {
		margin-right: auto;
		a {
			max-width: rem(122);
			width: 100%;
			display: block;
		}
	}

	&_nav {
		margin-right: rem(122);

		@include min {
			display: none;
		}
		> ul {
			display: flex;
			flex-wrap: wrap;
			> li {
				position: relative;
				margin-right: rem(48);
				display: flex;
				align-items: center;
				&:last-child {
					margin-right: 0;
				}

				&:hover {
					> a {
						color: $pink;
					}
					> i {
						color: $pink;
					}
				}
				> i {
					font-size: rem(10);
					margin-left: rem(10);
				}
				> a {
					font-weight: 600;

					&:hover {
						color: $pink;
					}
				}
			}
			> li.active {
				> a {
					color: $pink;
				}
				.icon-arrow-bottom {
					color: $pink;
					&::before {
						content: "\e903";
					}
				}

				.header_nav-open {
					display: block;
				}
			}
		}
	}

	&_right {
		

		@include min {
			display: flex;
			flex-wrap: wrap;
		}

		@include min768 {
			.button-i {
				display: none;
			}
		}
	}

	&_burger {
		display: none;

		@include min {
			margin-left: rem(24);
			display: flex;
			align-items: center;
			justify-content: center;
			width: rem(48);
			height: rem(48);
			border-radius: rem(14);
			background-color: $pink;

			.icon-cancel {
				color: white;
				font-size: rem(16);
			}

			.line {
				position: relative;
				width: rem(18);
				height: rem(12);
				display: flex;
				flex-direction: column;
				justify-content: space-between;

				

				&::before, &::after {
					content: "";
					width: 100%;
					background-color: white;
					height: rem(2);
					display: block;
				}
			}

			.center {
				width: 100%;
				background-color: white;
				height: rem(2);
				display: block;
			}
		}
	}
}

.menu_small {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow-y: auto;
	background: linear-gradient(180deg, #F2E7DC 0%, rgba(242, 231, 220, 1) 100%);
	z-index: 100;
	padding: rem(10);

	display: none;
	flex-direction: column;


	&-header {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		margin-bottom: rem(16);

		.logo {
			max-width: rem(120);
		}

		.close {
			width: rem(32);
			height: rem(32);
			background-size: rem(32);
			background: url(../img/close.png) no-repeat;
		}
	}

	&-content {
		display: flex;
		flex-direction: column;
		
		ul {
			margin-bottom: rem(20);
			li {
				margin-bottom: rem(18);
				display: flex;
				align-items: center;

				a {
					font-size: rem(18);
					line-height: rem(24);
					font-weight: 600;
					color: $pink;
					
					
				}

				i {
					margin-left: rem(8);

					font-size: rem(10);
					color: $pink;
				}
			}
		}

		.button-i {
			margin-top: auto;
		}
	}

	&-footer {
		margin-top: auto;

		.button-i {
			.border {
				margin-left: auto;
			}
		}
	}
}

.menu_small.active {
	@include min {
		display: flex;
	}
}

.scroll_off {
	overflow: hidden;
}

.header_nav-open {
	position: absolute;
	top: rem(40);
	left: 0;
	border-radius: rem(14);
	padding: rem(32) rem(24);
	border: rem(1) solid $pink;
	background-color: white;
	box-shadow: 0px 24px 32px rgba(209, 194, 194, 0.14),
	0px 24px 56px rgba(209, 194, 194, 0.12);
	z-index: 10;

	display: none;

	ul{
		li{
			margin-bottom: rem(16);

			&:last-child {
				margin-bottom: 0;
			}
			a {
				white-space: nowrap;
				font-size: rem(14);
				line-height: rem(20);
				color: #3D4039;

				&:hover {
					color: $pink;
				}
			}
		}
	}
}

.header-fixed {
	position: fixed;
	top: 0;
	width: 100%;
	height: 100%;
	height: rem(73);
	background: white !important;
	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
		0px 16px 32px rgba(209, 194, 194, 0.08);
	z-index: 10;

	.container {
		height: 100%;
		padding: rem(10) 0;
	}
}