.section_guarantee {
	position: relative;
	width: 100%;
	margin-bottom: rem(170);
	background: linear-gradient(180deg, rgba(242, 231, 220, .4) 0%, rgba(242, 231, 220, 0) 100%);
	padding-top: rem(120);

	@include min {
		margin-bottom: rem(80);
	}

	@include min768 {
		padding-top: rem(68);
	}

	// &::after {
	// 	content: "";
	// 	position: absolute;
	// 	left: 0;
	// 	bottom: rem(124);
	// 	width: rem(101);
	// 	height: rem(124);
	// 	background-size: rem(124);
	// 	display: block;
	// 	background: url(../img/tr/vector3.png) no-repeat;

	// 	@include min {
	// 		bottom: auto;
	// 		top: 0;
	// 		left: rem(-50);
	// 		transform: rotateY(180deg);
	// 	}

	// 	@include min768 {
	// 		display: none;
	// 	}
	// }

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include min {
			position: relative;
		}
	}

	&-left {
		margin-right: rem(128);

		@include min {
			margin-right: 0;
			position: absolute;
			top: 0;
			right: rem(100);
			width: rem(210);
			height: rem(248);
		}

		@include min768 {
			right: 50%;
			top: rem(120);
			margin-right: rem(-105);
			// margin-top: rem(-124);
		}

	}

	&-right {
		flex: 1 0 0;

		@include min768 {
			width: 100%;
		}

		.text-title {
			margin-bottom: rem(32);

			@include min {
				max-width: rem(300);
				margin-bottom: rem(48);
			}

			@include min768 {
				max-width: 100%;
				margin-bottom: rem(312);
				text-align: center;
			}
		}

		.descr {
			font-size: rem(24);
			line-height: rem(34);

			@include min {
				margin-bottom: rem(26);
			}

			@include min768 {
				font-size: rem(18);
				line-height: rem(24);
			}

			a {
				color: $pink;

				&:hover {
					text-decoration: underline;
				}
			}
		}

		.info {
			font-size: rem(18);
			line-height: rem(24);
			color: #80827F;
			margin-bottom: rem(40);

			@include min {
				font-size: rem(16);
				// line-height: rem();
				max-width: rem(360);
			}

			@include min768 {
				font-size: rem(14);
				line-height: rem(20);
			}
			a {
				color: $pink;

				&:hover {
					text-decoration: underline;
				}
			}
		}

		@include min {
			.block-ping-b {
				width: rem(580);

				
			}
		}

		@include min768 {
			.block-ping-b {
				width: calc(100% - 1.75rem);


			}
		}
	}
}

.block-ping-b {
	position: relative;
	padding: rem(28) rem(24) rem(24) rem(48);
	border-radius: rem(14);
	border: rem(1) solid $pink;
	margin-left: rem(28);

	.title {
		font-size: rem(18);
		color: $pink;
		margin-bottom: rem(8);
		display: block;
		font-weight: 600;

		@include min768 {
			font-size: rem(16);
		}
	}

	.text {
		font-size: rem(18);
		line-height: rem(24);
		color: #80827F;

		@include min {
			font-size: rem(16);
		}

		@include min768 {
			font-size: rem(14);
		}
	}

	.button-close {
		position: absolute;
		top: rem(12);
		left: rem(-28);

		width: rem(56);
		height: rem(56);
		background-color: white;
		box-shadow: 0px 16px 24px #EBDFDF;
		border-radius: rem(14);

		display: flex;
		align-items: center;
		justify-content: center;

		i {
			font-size: rem(32);
			color: $pink;
		}

		.close {
			width: rem(32);
			height: rem(32);
		}
	}
}

.close {
	width: 100%;
	height: 100%;
	background: url(../img/close.png) no-repeat;
}