.slide_menu {
	position: fixed;
	top: 0;
	right: 0;
	transform: translateX(100%);
	background-color: white;
	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	width: rem(420);
	padding-top: rem(21);
	padding-right: rem(24);
	padding-bottom: rem(40);
	z-index: 200;
	height: 100%;

	overflow-y: auto;

	transition: all 400ms ease;

	display: flex;
	flex-direction: column;

	@include min540 {
		width: 100%;
		padding-right: rem(10);
		padding-top: rem(16);
		padding-bottom: rem(32);
		
	}
	.logo {
		display: none;

		@include min540 {
			display: flex;
			max-width: rem(128);
			padding-left: rem(10);
			margin-right: auto;
		}
	}
	&-top {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: flex-end;
		margin-bottom: rem(38);

		.sm-button {
			@include min540 {
				position: absolute;
				bottom: rem(20);
				left: rem(30);
			}
		}
	}

	&-list {

		> ul {

			>li {
				position: relative;
				padding-left: rem(30);
				margin-bottom: rem(20);
				// border-left: rem(1) solid transparent;
				transition: all 300ms ease;

				&::before {
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					width: rem(2);
					height: rem(24);
					background-color: $pink;
					opacity: 0;
				}

				>a {
					font-size: rem(18);
					line-height: rem(24);
					font-weight: 600;
					
				}

				&:hover {
					// border-color: $pink;

					&::before {
						opacity: 1;
					}

					a {
						color: $pink;
					}
				}

				.tab-menu {
					margin-left: rem(16);
					margin-top: rem(16);

					li {
						margin-bottom: rem(10);
						padding-left: 0;
						border: 0;

						a {
							font-size: rem(14);
							color: #80827F;
							font-weight: 600;
							line-height: rem(20);

							&:hover {
								color: $pink;
							}
						}
					}
				}
			}
			li.active {
				color: $pink;

				a {
					color: $pink;
				}
			}
		}
	}

	&-foot {
		margin-top: auto;
		padding-left: rem(30);
		ul {

			display: flex;
			flex-wrap: wrap;
			margin-bottom: rem(32);

			li {

				&:first-child {
					margin-right: rem(24);
				}

				a {
					font-size: rem(16);
					font-weight: 600;
				}
			}
		}

		.footer_social {
			margin-right: 0;
			justify-content: flex-start;
		}
	}
}

.slide_menu.active {
	transform: translateX(0%);
}