.lk_page {
	background: linear-gradient(180deg, rgba(242, 231, 220, .4) 0%, rgba(242, 231, 220, 0) 100%);
}

.user_video {
	max-width: rem(880);
	height: rem(496);
	border-radius: rem(14);
	overflow: hidden;
	margin-bottom: rem(40);
}

.user_title-20 {
	font-size: rem(20);
	line-height: rem(32);
	margin-bottom: rem(16);
}
.user_text {
	font-size: rem(16);
	line-height: rem(22);
	margin-bottom: rem(24);
}

.user_header {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	margin-bottom: rem(32);
	margin-top: rem(46);

	@include min {
		margin-top: rem(24);
		margin-bottom: rem(24);
	}

	&-logo {
		display: none;
		max-width: rem(130);

		@include min {
			display: block;
		}
	}

	&-user {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		@include min {
			display: none;
		}

		&-image {
			margin-right: rem(24);
			border-radius: rem(14);
			width: rem(64);
			height: rem(64);
			box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
			0px 16px 32px rgba(209, 194, 194, 0.08);
			// overflow: hidden;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.title {
			font-size: rem(18);
			line-height: rem(24);
			font-weight: 600;
		}
	}

	.header_burger {
		display: none;

		@include min {
			display: flex;
		}
	}

	&-button {
		@include min {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
		}
	}
}

.user_wrap {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	max-width: rem(1360);
	margin: 0 auto;
	padding-bottom: rem(20);
	height: 100%;

	@include tablet {
		max-width: rem(1280);
	}

	@include min {
		max-width: 100%;
		width: calc(100% - 48px);
	}

	@include min540 {
		width: calc(100% - 20px);
	}

	&-content {
		margin-left: rem(40);
		flex: 1 0 0;

		.item_select {
			height: auto;
			margin-bottom: rem(24);
			padding-top: rem(28);
			// padding-bottom: rem(28);
			padding-right: rem(24);
			overflow: hidden;

			@include min {
				height: rem(312);
			}

			@include min540 {
				height: rem(110);
				padding-top: rem(14);
				padding-bottom: rem(14);
				padding-right: rem(12);
			}

			.item_select-left {
				flex: 0 0 rem(137);
				width: rem(137);
				margin-right: rem(24);

				@include min {
					flex: 0 0 rem(256);
					width: rem(256);

					
				}
				@include min540 {
					flex: 0 0 rem(120);
					width: rem(120);
					height: rem(100);
					margin-right: 0;
					display: flex;
					justify-content: center;
				}

				@include min350 {
					flex: 0 0 rem(100);
					width: rem(100);
				}
			}

			.item_select-right {
				flex: 1 0 0;
				// padding-left: rem(150);

				@include min540 {
					flex: 0 0 60%;
				}

				.text {
					margin-bottom: rem(24);
				}

				.text-bottom {
					font-size: rem(12);
					line-height: rem(22);
					margin-top: rem(12);
					padding-bottom: rem(24);
					width: 100%;
					display: block;

					span {
						color: $pink;
					}
				}
			}
			
			.item_select-left img {
				// width: auto;
				position: relative;
				top: auto;
				// height: rem(345);
				width: 100%;
				height: 100%;
				object-fit: cover;

				@include min540 {
					height: 100%;
					width: 100%;
					object-fit: cover;
					object-position: right;
				}
			}

			.button-default-b {
				margin-right: rem(8);
				padding: rem(10) rem(15);
			}
			.button-white {
				padding: rem(12) rem(20) rem(14) rem(20);
			}
			.button-default-b,
			.button-white {
				font-size: rem(14);
				display: flex;
				align-items: center;
			}
		}

		.smart-block {
			.button-default-b {
				margin-right: rem(8);
				padding: rem(10) rem(15);
			}

			.button-white {
				padding: rem(12) rem(20) rem(14) rem(20);
			}

			.button-default-b,
			.button-white {
				font-size: rem(14);
				display: flex;
				align-items: center;
			}

			.text-bottom {
				font-size: rem(12);
				line-height: rem(22);
				margin-top: rem(12);
				padding-bottom: rem(24);
				width: 100%;
				display: block;

				@include min540 {
					margin-top: 0;
				}

				span {
					color: $pink;
				}
			}
		}

		.item_select.locked {
			position: relative;
			&::after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: rgba(218, 90, 135, 0.2);
				backdrop-filter: blur(2px);
			}

			.locked-icon {
				z-index: 1;
				position: absolute;
				top: 50%;
				left: 50%;
				margin-left: rem(-20);
				margin-top: rem(-40);
				width: rem(80);
				height: rem(80);
				border-radius: rem(14);
				background-color: white;
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: center;
				box-shadow: 0px 4px 8px rgba(218, 90, 135, 0.12),
				0px 16px 32px rgba(218, 90, 135, 0.08);
				

				@include min540 {
					margin-left: rem(-35);
				}

				.text {
					font-size: rem(16);
					line-height: rem(20);
					margin-top: rem(12);
					width: 100%;
				}

				i {
					font-size: rem(48);
				}
			}

			.locked-icon.timer {
				width: rem(144);
				height: auto;
				padding: rem(16);

				margin-left: rem(-60);

				@include min540 {
					width: rem(108);
					margin-left: rem(-50);
					padding: rem(12);

					i {
						font-size: rem(32);
					}

					.text {
						font-size: rem(12);
						line-height: rem(14);
						white-space: nowrap;
					}
				}
			}

			.item_select-left img {
				z-index: 0;
			}
		}

		@include min {
			margin-left: 0;
		}
	}
	
	.block_faq {
		margin-top: 0;
	}
	.faq_item-header {
		padding: rem(16) 0;
	}
}


.user_aside {
	width: rem(258);
	overflow: hidden;
	background-color: white;
	border-radius: rem(14);
	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);

	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	min-height: 100%;

	max-height: rem(832);

	margin: rem(16) 0;
	padding-bottom: rem(20);

	@include min {
		display: none;
	}

	&-logo {
		width: 100%;
		padding-top: rem(36);
		padding-left: rem(32);
		padding-bottom: rem(64);

		a {
			width: rem(132);
			display: block;
		}
	}

	&-nav {
		width: 100%;
		padding-left: rem(24);
		padding-right: rem(4);
		margin-bottom: rem(24);

		ul {
			li {
				margin-bottom: rem(16);
				border-right: rem(2) solid transparent;
				transition: all 400ms ease;

				&:hover {
					border-color: $pink;

					a {
						color: $pink;
					}
				}

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-size: rem(16);
					font-weight: 600;
					display: block;
					padding: rem(3) 0;
				}
			}
			li.active {
				border-color: $pink;

				a {
					color: $pink;
				}
			}
		}
	}

	&-footer {
		width: 100%;
		margin-top: auto;
		padding-left: rem(24);

		.footer_nav {
			width: 100%;
		}
		
		.footer_social {
			justify-content: flex-start;
			margin-top: rem(32);
		}
	}
}


.back_link {
	display: flex;
	align-items: center;
	color: $pink;
	margin-bottom: rem(32);
	font-size: rem(12);
	line-height: rem(16);

	i {
		margin-right: rem(8);
		font-size: rem(16);
	}
}

.user_title {
	font-size: rem(32);
	line-height: rem(40);
	font-weight: 600;
	margin-bottom: rem(32);

	@include min540 {
		font-size: rem(24);
		line-height: rem(34);
		margin-bottom: rem(24);
	}
}

.user_tabs {

	@include min {
		.w-6\/12 {
			width: 100%;

			&:first-child {
				margin-bottom: rem(24);
			}
		}
	}

	&-nav {
		margin-bottom: rem(24);

		ul {
			display: flex;
			flex-wrap: wrap;
			border-bottom: rem(1) solid #DED6CF;
			width: 100%;

			li {
				margin-right: rem(40);

				padding-bottom: rem(24);
				border-bottom: rem(1) solid transparent;

				@include min540 {
					margin-right: rem(32);
					padding-bottom: rem(16);
				}
				
				&:hover {
					border-color: $pink;

					a {
						color: $pink;
					}
				}
				&:last-child {
					margin-right: 0;
				}

				a {
					font-size: rem(16);
					line-height: rem(20);
					color: #A4A4A4;
					display: block;

					@include min540 {
						font-size: rem(14);
						line-height: rem(16);
					}
				}
			}

			li.active {
				border-color: $pink;

				a {
					color: $pink;
				}
			}
		}
	}

	&-block {
		margin-bottom: rem(32);

		&:last-of-type {
			margin-bottom: 0;
		}

		&-title {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin-bottom: rem(16);

			.user_title-block {
				margin-right: rem(16);
				margin-bottom: 0;
			}
		}
	}

	&-block.locked {
		.user_tabs-item {
			opacity: .5;
			pointer-events: none;
		}
	}
}

.text-small {
	font-size: rem(14);
}

.user_title-block {
	font-size: rem(24);
	line-height: rem(30);
	font-weight: 600;
	margin-bottom: rem(16);

	@include min540 {
		font-size: rem(20);
		line-height: rem(26);
	}
}

.user_tabs-item {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	border-radius: rem(14);
	padding: rem(4);
	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	background-color: white;
	transition: all 300ms ease;
	border: rem(1) solid transparent;

	margin-bottom: rem(4);

	&:last-of-type {
		margin-bottom: 0;
	}

	@include min540 {
		// flex-wrap: nowrap;

		.button-b-pink {
			margin-top: rem(12);
			flex: 0 0 100%;
		}
	}

	&:hover {
		box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
		0px 16px 32px rgba(209, 194, 194, 0.08);
		border-color: rgba(218, 90, 135, 0.2);

		.user_text {
			color: $pink;
		}

		// .button-b-pink {
		// 	color: white;
		// 	background-color: $pink;

		// 	i {
		// 		color: white;

		// 		&::before {
		// 			color: white;
		// 		}
		// 	}
		// }
	}

	.user_b-item {
		margin-right: rem(16);
		@include min540 {
			white-space: nowrap;
			margin-right: rem(8);
			
		}
	}
	.user_text {
		margin-right: auto;

		@include min540 {
			flex: 1 0 0;
			max-width: 70%;
		}
	}
}

.user_text {
	font-size: rem(16);
}

.user_group-block {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-bottom: rem(32);

	&_left {
		margin-right: rem(24);
		max-width: 31%;

		@include min {
			max-width: 100%;
			margin-right: 0;
			margin-bottom: rem(16);
		}

		.user_title {
			margin-bottom: 0;
		}
	}

	&_right {
		max-width: rem(520);

		@include min {
			max-width: rem(580);
		}

		@include min540 {
			max-width: 100%;
		}

		.text {
			font-size: rem(16);
			line-height: rem(22);

			@include min540 {
				font-size: rem(14);
				line-height: rem(20);
			}
		}
	}
}

@include min {
	.md-gift_item {
		box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
		0px 16px 32px rgba(209, 194, 194, 0.08);
		border-radius: rem(14);
		background-color: white;
		overflow: hidden;
	}
}

.user_gift-item {
	position: relative;
	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	border-radius: rem(14);
	background-color: white;
	padding: rem(32) 0;
	max-width: 31%;
	width: 100%;
	// flex-grow: 1;
	// flex-shrink: 0;
	display: flex;
	flex-direction: column;
	align-items: center;

	margin-right: rem(24);

	@include min {
		max-width: 100%;
		align-items: flex-start;
		padding: rem(32) rem(40);
		margin-right: 0;
		box-shadow: none;
		border-radius: 0;
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}

	@include min540 {
		padding: rem(32) rem(16);
	}

	&:nth-child(3n) {
		margin-right: 0;
	}

	img {
		height: rem(140);
		margin-bottom: rem(16);

		@include min {
			order: 2;
			height: rem(160);
			margin-right: rem(110);
		}

		@include min540 {
			margin-right: 0;
			height: rem(120);
		}

		// @include min {
		// 	position: absolute;
		// 	bottom: 0;
		// 	right: 0;
		// }
	}

	.title {
		font-size: rem(24);
		line-height: rem(32);
		font-weight: 600;
		margin-bottom: rem(8);

		@include min540 {
			font-size: rem(20);
			line-height: rem(26);
		}
	}
	.text {
		color: $pink;
		font-size: rem(18);
		line-height: rem(24);
		max-width: rem(225);
		text-align: center;

		@include min540 {
			font-size: rem(16);
			line-height: rem(22);
			max-width: rem(160);
			text-align: left;
		}
	}
}

.user_gift-block {
	position: relative;
	border-radius: rem(14);
	padding: rem(24) 0 rem(24) rem(40);
	background-color: white;
	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	margin-bottom: rem(24);

	@include min {
		padding: rem(24) 0 rem(24) rem(24);
	}

	@include min540 {
		padding: rem(16) 0 rem(16) rem(16);
	}

	&_left {
		max-width: rem(500);
		.title {
			font-size: rem(24);
			line-height: rem(30);
			margin-bottom: rem(8);

			@include min540 {
				font-size: rem(20);
				line-height: rem(26);
				max-width: rem(210);
			}

			span {
				color: $pink;
			}
		}

		@include min540 {
			.text-small {
				font-size: rem(12);
				line-height: rem(18);
			}
		}
	}

	&_right {
		position: absolute;
		bottom: 0;
		right: 0;

		@include min {
			width: rem(217);
		}

		@include min540 {
			width: rem(100);
			bottom: auto;
			top: 0;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			vertical-align: top;
		}
	}
}

.user_b-item {
	font-size: rem(18);
	line-height: rem(22);
	font-weight: 600;
	color: $pink;
	background: rgba(218, 90, 135, 0.1);
	border-radius: rem(12);
	padding: rem(9) rem(12);
	// height: rem(40);

	i {
		font-size: rem(24);
	}

	@include min540 {
		font-size: rem(12);
		line-height: rem(20);
		// height: rem(38);
	}
}

.button-b-white {
	font-size: rem(14);
	font-weight: 600;
	line-height: rem(20);
	padding: rem(10) rem(16);
	// border: rem(1) solid $pink;
	background-color: white;
	// border-radius: rem(12);
	color: $pink;
	transition: all 300ms ease;
	display: flex;
	align-items: center;

	@include min540 {
		width: 100%;
		justify-content: center;
	}

	i {
		margin-left: rem(8);
		font-size: rem(24);
	}

	// &:hover {
	// 	color: white;
	// 	background-color: $pink;

	// 	i {
	// 		color: white;

	// 		&::before {
	// 			color: white;
	// 		}
	// 	}
	// }
}

.button-b-pink {
	font-size: rem(14);
	font-weight: 600;
	line-height: rem(20);
	padding: rem(10) rem(16);
	border: rem(1) solid $pink;
	background-color: white;
	border-radius: rem(12);
	color: $pink;
	transition: all 300ms ease;
	display: flex;
	align-items: center;

	@include min540 {
		width: 100%;
		justify-content: center;
	}

	i {
		margin-left: rem(8);
	}

	&:hover {
		color: white;
		background-color: $pink;

		i {
			color: white;

			&::before {
				color: white;
			}
		}
	}
}


// UI

.user_form-block {
	border-radius: rem(14);
	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	background-color: white;
	padding: rem(32) rem(24);

	@include min540 {
		padding: rem(24) rem(16);

		.user_title-block {
			text-align: center;
		}
	}
}
.user_form-block.locked {
	opacity: .5;
	pointer-events: none;
}

.field {
	border-radius: rem(14);
	background-color: white;
	border: rem(1) solid #E1D5D9;
	padding: rem(14) rem(24);
	font-size: rem(14);
	color: #3D4039;
	width: 100%;
	font-family: 'Gilroy';

	@include placeholder {
		color: #A4A4A4;
	}
}
textarea.field {
	min-height: rem(96);
	max-height: rem(240);
	resize: vertical;
}
.field-text {
	position: relative;

	.field {
		padding-right: rem(50);
	}

	.text {
		position: absolute;
		top: rem(14);
		right: rem(24);
		font-size: rem(14);
		color: #A4A4A4;
		pointer-events: none;
	}
	a.text {
		pointer-events: visible;
		font-size: rem(12);
		color: $pink;
	}
}
.field-set {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(16);

	&:last-of-type {
		margin-bottom: 0;
	}

	&_label {
		font-size: rem(12);
		line-height: rem(16);
		font-weight: 600;
		margin-bottom: rem(8);
	}

}
.field-group {
	display: flex;
	width: 100%;
	// flex-wrap: wrap;

	.field-set {
		flex: 1 0 0;
		margin-right: rem(16);

		.field-text {
			width: auto;
			
		}

		&:last-child {
			margin-right: 0;
		}
	}
}

.field-upload {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	.upload-select {
		cursor: pointer;
		border: rem(1) solid $pink;
		border-radius: rem(14);
		padding: rem(10) rem(16);

		i {
			margin-left: rem(8);
			font-size: rem(14);
		}

		.text {
			font-size: rem(14);
			font-weight: 600;
			color: $pink;
		}

		input[type="file"] {
			display: none;
		}
	}

	.upload-result {
		margin-left: rem(14);
		font-size: rem(12);
		line-height: rem(16);
		color: #80827F;
	}
}

.field-checked {

	
	margin-bottom: rem(8);

	&:last-of-type {
		margin-bottom: 0;
	}

	.checked {
		cursor: pointer;
		
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		

		input {
			display: none;
		}

		input[type="checkbox"] {
			~ .check {
				position: relative;
				width: rem(20);
				height: rem(20);
				border-radius: rem(3);
				border: rem(1) solid $pink;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: white;
			}

			&:checked {
				~ .check {
					&::before {
						content: "\e912";
						color: $pink;
						font-family: 'icons';
						font-size: rem(10);
					}
				}
			}
		}

		input[type="radio"] {
			~.check {
				position: relative;
				width: rem(20);
				height: rem(20);
				border-radius: 50%;
				border: rem(1) solid #E1D5D9;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: white;
			}

			&:checked {
				~.check {
					&::before {
						content: "";
						border-radius: 50%;
						width: rem(10);
						height: rem(10);
						background-color: $pink;
						// color: $pink;
						// font-family: 'icons';
						// font-size: rem(10);
					}
				}
			}
		}
		

		.text {
			font-size: rem(14);
			line-height: rem(20);
			margin-left: rem(8);

			@include min540 {
				flex: 1 0 0;
			}
		}
	}

	.checked.text-top {
		@include min540 {
			align-items: flex-start;
		}
	}

	
}

